
/* Section 3: Chat Area */
.chat-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}

.back-button {
  display: none;
}

.chat-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 20px;
}

.chat-room-info {
  flex-grow: 1;
}

.chat-room-info h3 {
  margin: 0;
  font-size: 1.1rem;
}

.chat-messages {
  flex-grow: 1;
  padding: 15px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.chat-message {
  margin-bottom: 7px;
  padding: 15px;
  max-width: 70%;
  word-wrap: break-word;
  overflow-wrap: break-word; 
  word-break: break-word;
  white-space: pre-wrap; 
  display: block;
  display: flex;
  align-items: center;
}

.chat-message.sent {
  background-color: var(--secondary-accent-color);
  border: 1px solid #ddd;
  margin-left: auto;
  border-radius: 15px 0 15px 15px;
}

.chat-message.received {
  background-color: var(--text-color);
  border: 1px solid #ddd;
  margin-right: auto;
  border-radius: 0 15px 15px 15px;
}

.message-image {
  max-width: 100%; 
  height: auto;
  border-radius: 10px;
  margin-top: 5px;
}

.message-video {
  max-width: 100%;
  max-height: 300px;
  height: auto; 
  border-radius: 10px;
  margin-top: 5px;
}

.message-pdf {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

.message-pdf img {
  max-width: 100px;
  height: auto;
}

.message-sender {
  font-weight: 600;
  font-size: 1rem;
  margin-bottom: 5px;
  color:var(--contrast-color);
}

.chat-input {
  display: flex;
  padding: 10px;
  background-color: var(--text-color);
  border-top: 1px solid #ddd;
  align-items: center;
}

.chat-input input {
  flex-grow: 1;
  font-size: 1rem;
  padding: 15px;
  border: none;
  border-radius: 20px;
  background-color: #f0f0f0;
  outline: none;
}

.chat-input button {
  padding: 5px;
  border: none;
  background-color: transparent;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  width: 40px;
  height: 40px;
  margin: 0 10px;
}

.chat-input img {
  width: 30px;
  height: 30px;
}